export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased bg-[#0D171F]`}>
        <Navbar />
        <div className="flex">
          <Sidebar />
          <main className="flex-1">
            <div className="p-5 pt-[6%]"> {/* Ajusta el pt seg√∫n necesidad */}
              {children}
            </div>
            <div className="p-5">
              <Footer />
            </div>
          </main>
        </div>
      </body>
    </html>
  );
}